.page__body {
  font-family: var(--basic-font);
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  padding: 0;
  margin: 0;
  color: var(--basic-fcolor);
  background-color: var(--basic-background);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  counter-reset: section;
}

.page__leveler {
  margin-left: 20px;
  margin-right: calc(20px - var(--scrollbar));

  @media (min-width: $tablet-width) {
    margin-left: 30px;
    margin-right: calc(30px - var(--scrollbar));
  }

  @media (min-width: $desktop-width) {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 30px;
  }
}
